.header {

  &__bg {
    position: relative;
    height: 100%;
    top: 0;
    background: url(../images/bg.jpg) no-repeat;
    background-position: top;
    background-size: 100%;
    background-attachment: fixed;

    &_2 {
      background: url(../images/bg2.jpg) no-repeat;
      background-position: right -100px;
      background-size: 100%;
      background-attachment: fixed;
    }

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      content: "";
      background-color: rgba(0, 0, 0, 0.5);
    }

  }

  &__container {
    position: relative;
    padding-bottom: 40px;
  }

  &__row {

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__c-3-12 {
    display: block;
    float: left;
    width: 33.33%;
  }
  &__c-6-12 {
    position: relative;
    display: block;
    float: left;
    width: 66.66%;
  }

  &__logo {
    display: inline-block;
    margin: 50px 0 0 0;
    text-decoration: none;

    & img {
      width: 380px;
      height: auto;
      padding-bottom: 5px;
    }

    & span {
      display: block;
      padding-top: 5px;
      font-size: 19px;
      font-weight: 300;
      line-height: 22px;
      color: #fff;
      border-top: 1px solid rgba(255, 255, 255, 0.5);
      text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
    }

  }

  &__main-nav-btn {
    position: relative;
    //z-index: 1;
    //left: 10px;
    display: none;
    float: right;
    margin: 50px 0 0;
    color: #fff;
    background: #3BC391;
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    padding: 15px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
    cursor: pointer;

    &:hover {
      background: #33a97e;
    }

  }

  &__main-nav {
    z-index: 10;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__menu {
    list-style: none;
    line-height: 36px;
    padding: 10px 0 0 0;
    margin: 0;
    float: right;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

    & > li {
      position: relative;
      float: left;
      padding-right: 10px;

      &:last-child {
        padding-right: 0;
      }

      & > a {
        display: block;
        padding: 10px;
        font-weight: 400;
        font-size: 16px;
        text-transform: uppercase;
        text-decoration: none;
        color: #fff;
        transition: all .2s;
        text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);

        &:hover {
          color: #3BC391;
        }

      }

    }

  }

  &__menu > li:hover > a {
    color: #3BC391;
  }

  &__menu > li:hover &__menu-sub {
    display: block;
  }

  &__menu-sub {
    display: none;
    position: absolute;
    left: 0;
    list-style: none;
    background: #fff;
    padding: 10px 0;
    z-index: 1;
    border-radius: 2px;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);

    animation: animationFrames linear 0.2s;
    animation-iteration-count: 1;
    transform-origin: 50% 50%;
    -webkit-animation: animationFrames linear 0.2s;
    -webkit-animation-iteration-count: 1;
    -webkit-transform-origin: 50% 50%;
    -moz-animation: animationFrames linear 0.2s;
    -moz-animation-iteration-count: 1;
    -moz-transform-origin: 50% 50%;
    -o-animation: animationFrames linear 0.2s;
    -o-animation-iteration-count: 1;
    -o-transform-origin: 50% 50%;
    -ms-animation: animationFrames linear 0.2s;
    -ms-animation-iteration-count: 1;
    -ms-transform-origin: 50% 50%;

    & > li {
      display: block;

      & > a {
        padding: 5px 15px;
        font-weight: 400;
        font-size: 16px;
        white-space: nowrap;
        text-decoration: none;
        color: #333;
        transition: all .2s;

        &:hover {
          color: #3BC391;
        }

      }

    }
  }

  @keyframes animationFrames{
    0% {
      opacity:0;
      transform:  translate(0px,-10px)  ;
    }
    100% {
      opacity:1;
      transform:  translate(0px,0px)  ;
    }
  }

  @-moz-keyframes animationFrames{
    0% {
      opacity:0;
      -moz-transform:  translate(0px,-10px)  ;
    }
    100% {
      opacity:1;
      -moz-transform:  translate(0px,0px)  ;
    }
  }

  @-webkit-keyframes animationFrames {
    0% {
      opacity:0;
      -webkit-transform:  translate(0px,-10px)  ;
    }
    100% {
      opacity:1;
      -webkit-transform:  translate(0px,0px)  ;
    }
  }

  @-o-keyframes animationFrames {
    0% {
      opacity:0;
      -o-transform:  translate(0px,-10px)  ;
    }
    100% {
      opacity:1;
      -o-transform:  translate(0px,0px)  ;
    }
  }

  @-ms-keyframes animationFrames {
    0% {
      opacity:0;
      -ms-transform:  translate(0px,-10px)  ;
    }
    100% {
      opacity:1;
      -ms-transform:  translate(0px,0px)  ;
    }
  }


  &__contact {

    &-info {
      display: block;
      padding: 40px 0 0 0;
      text-align: right;
      font-size: 14px;
      color: #fff;
      line-height: 30px;
      text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);

      & a {
        text-decoration: none;
        color: #fff;
      }

    }

    &-tel {
      position: relative;

      &:before {
        content: "";
        position: absolute;
        left: -25px;
        top: 3px;
        width: 16px;
        height: 16px;
        background: url(../images/call-answer.png) no-repeat;
      }

    }

    &-email {
      position: relative;

      &:before {
        content: "";
        position: absolute;
        left: -25px;
        top: 3px;
        width: 16px;
        height: 16px;
        background: url(../images/envelope.png) no-repeat;
      }

    }

  }

  &__slogan {
    position: relative;
    margin: 0 auto;
    padding: 130px 0 220px 0;
    text-align: center;
  }

  &__hot {

    & a {
      vertical-align: middle;
      display: inline-block;
      margin: 20px 80px 0 0;
      text-align: center;
      text-decoration: none;
      color: #fff;
      transition: all .2s;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        color: #33a97e;
      }

      & img {
        width: 85px;
        height: 85px;
      }

      & h2 {
        font-weight: 400;
        font-size: 14px;
        line-height: normal;
        text-transform: uppercase;
        text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
      }

    }

  }

}

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .header {
    background: #fff;

    &__bg {
      background-position: center;
      background-size: auto 100%;
    }

    &__slogan {
      position: relative;
      //top: 0;
      //width: 200px;
      padding: 50px 20px 100px 20px;
      text-align: center;
    }

    &__hot {

      & a {

        & img {
          width: 70px;
          height: 70px;
        }

      }

    }

    &__main-nav-btn {
      display: inline-block;
    }

    &__main-nav {
      display: none;
      background: #3BC391;
      z-index: 4;
      transition: all .1s ease-out;

      &_open {
        display: block;
      }

    }

    &__menu {
      display: block;
      padding: 20px;
      margin: 0;
      background: #fff;

      & > li {
        display: block;
        float: left;
        width: 100%;
        padding: 0 0 20px 0;

        & > a {
          display: block;
          color: #333;
          text-shadow: none;
          padding-bottom: 0;
          font-weight: 600;
        }

      }

    }

    &__menu > li:hover > a {
      color: #333;
    }

    &__menu-sub {
      position: relative;
      display: block;
      width: 100%;
      float: left;
      background: none;
      box-shadow:none;
      animation: none;
      padding-left: 20px;
    }

    &__contact-info {
      padding: 0;
    }

    &__logo {
      //display: block;
      //margin: 50px 40px 20px 40px;

      & img {
        width: 300px;
      }

      & span {
        padding-top: 5px;
        font-size: 16px;
      }

    }

    &__c-3-12 {
      width: 100%;
    }
    &__c-6-12 {
      width: 100%;
    }
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {
  .header {

    &__contact-info {
      //padding: 5px 20px 20px 20px;
      font-size: 14px;
      line-height: 22px;
    }

    &__logo {
      //display: block;
      //text-align: center;
      //margin: 0 auto;
      //padding: 40px 20px 20px 20px;

      & img {
        display: inline-block;
        width: 250px;
      }

      & span {
        display: block;
        border-top: 1px solid rgba(255, 255, 255, 0.5);
        padding-top: 5px;
        font-weight: 400;
        font-size: 14px;
      }

    }

    &__hot {
      text-align: center;

      & a {
        display: block;
        margin: 40px 0;

        & img {
          width: 70px;
          height: 70px;
        }

      }

    }

  }

}

